<template>
  <div class="page-box index-box">
    <HeadTop :title="'ABDX直接质押'"></HeadTop>
    <div class="swap-box">
       <div class="swap">
         <div class="swap-title flex-between">
          <div class="text">ABDX直接质押</div>
          <span>3.6X</span>
        </div>
         <div class="swap-input">
           <div class="title flex-between">
               <div class="text">输入<span class="total-stake" @click="getTotalBlance">全部质押</span></div>
               <div class="balance">余额: {{balance}}</div>
           </div>
             <van-field
                v-model="amount"
                type="number"
                label=""
                placeholder="0.0"
            >
                
                <template #button>
                  <div class="flex-middle right-box">
                    <img src="@/assets/images/ABDX_logo.png" alt="">
                    <span>ABDX</span>
                  </div>
                </template>
            </van-field>
         </div>
         <div class="swap-icon">
            <img src="@/assets/images/swap_icon.png" alt="">
         </div>
         <div class="swap-input">
           <div class="title flex-between">
               <div class="text">输出 (质押收益)</div>
           </div>
             <van-field
                v-model="incomeAmount"
                label=""
                readonly
                placeholder="0.0"
            >
                <template #button>
                  <div class="flex-middle right-box">
                    <img src="@/assets/images/ABDX_logo.png" alt="">
                    <span>ABDX</span>
                  </div>
                </template>
            </van-field>
         </div>
         <div class="intro-box" v-if="!userInfo.isExist">
           <div class="title">填写推荐人</div>
           <div class="flex-middle">
             <input class="flex-item intro" v-model="userInfo.intro" type="text" placeholder="请输入推荐人钱包地址">
           </div>
         </div>
         <div class="btn-box">
           <van-button type="primary" :loading="btn.loading" v-if="Number(allowance) > 0" size="large" loading-text="正在质押..."  @click="stake">质押</van-button>
          <van-button type="primary" :loading="btn.loading" v-else size="large" loading-text="正在授权..." @click="approve">授权</van-button>
          </div>
       </div>
    </div>
    <div class="rule-box">
        <div class="title">质押规则</div>
        <div class="content">
         1、ABDX直接进行质押，质押可获取3.6倍的收益，
          每天千分之二进行释放，500天释放完毕。<br>
          2、同样也可以通过推荐下级进行加速释放。<br>
          3、质押规则<br>
          第一轮：直接质押和兑换质押，ABDX加起来不能大于
          10WABDX<br>
          第二轮：第一轮出局之后，才能追加质押到20WABDX<br>
          第三轮：第二轮再次出局之后，才能追加质押到
          40WABDX<br>
          后面轮数，以此类推
        </div>
    </div>
  </div>
</template>
<script>
import HeadTop from '@/components/HeadTop' 
import { reactive, toRefs, computed, watch } from "vue";
import { useRouter } from 'vue-router'
import { useI18n } from "vue-i18n";
import {useStore} from "vuex";
import config from "@/utils/config";
import tokenAbi from "@/utils/abi";
import investAbi from "@/utils/investAbi";
import { MaxUint256, AddressZero } from "@ethersproject/constants";
import { Toast } from 'vant';
import { intro } from '@/storage'
import BigNumber from 'bignumber.js';
export default {
  components: {
    HeadTop
  },
  setup() {
    const store = useStore()
    const router = useRouter()
    const { t } = useI18n()
    const data = reactive({
      $t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      investContract: null,
      abdxContract: null,
      btn: {
        loading: false
      },
      userInfo: {
        intro: intro.get() || '',
        isExist: true,
        investAmmount: 0
      },
      amount: '',
      incomeAmount: computed(() => {
        if(data.amount) {
          return Number(data.amount * 3.6).toFixed(6)
        }
        return ''
      }),
      allowance: '',
      balance: '',
      rate: '100',
      staticAmount: '0.00',
      initContract: () => {
        data.investContract = new data.web3.eth.Contract(investAbi, config.investAddress)
        data.abdxContract = new data.web3.eth.Contract(tokenAbi, config.abdxAddress)
        data.initData()
      },
      initData: () => {
        data.getUserInfo()
        data.getRate()
        data.getBalance()
        data.getAllowance()
      },
      getUserInfo: async () => {
        let userInfo = await data.investContract.methods.getUserInfo(data.address).call()
        let decimals = await data.abdxContract.methods.decimals().call()
        let records = await data.investContract.methods.getUserRecords(data.address).call()
        console.log(records)
        data.userInfo.isExist = userInfo.isExist
        if(data.userInfo.isExist) {
          data.userInfo.intro = userInfo.intro
        }
        data.userInfo.investAmmount = new BigNumber(userInfo.investAmmount).dividedBy(10 ** decimals).toFixed(2)
      },
      getRate: async () => {
        let price = await data.investContract.methods.tokenPrice().call();
        data.rate = new BigNumber(price).dividedBy(10000).toFixed()
      },
      getAllowance: async () => {
        let allowance = await data.abdxContract.methods.allowance(data.address, config.investAddress).call()
        data.allowance = allowance;
        console.log(allowance, 'allowance------')
      },
      getBalance: async () => {
        let decimals = await data.abdxContract.methods.decimals().call()
        let balance = await data.abdxContract.methods.balanceOf(data.address).call()
        data.balance = new BigNumber(balance).dividedBy(10 ** decimals).toFixed(6)
      },
      getTotalBlance: async () => {
        let decimals = await data.abdxContract.methods.decimals().call()
        let balance = await data.abdxContract.methods.balanceOf(data.address).call()
        data.amount = new BigNumber(balance).dividedBy(10 ** decimals).toFixed(Number(decimals))
      },
      approve: async () => {
        data.btn.loading = true
        try {
          await data.abdxContract.methods.approve(config.investAddress, MaxUint256).send({from: data.address})
          Toast('授权成功')
          data.getAllowance()
          data.btn.loading = false
        } catch (error) {
          Toast('授权失败')
          data.btn.loading = false
        }
        
      },
      stake: async () => {
        if(!data.amount) {
          Toast('请先输入数量')
          return
        }
        if(Number(data.amount) > Number(data.balance)) {
          Toast('余额不足')
          return
        }
        if(Number(data.amount) < 10000 && Number(data.userInfo.investAmmount) <= 0) {
          Toast('最少质押10000ABDX')
          return
        }
        if(!data.userInfo.isExist && (data.userInfo.intro == '' || data.userInfo.intro == AddressZero)) {
          Toast('请先填写推荐人')
          return
        }
        let introUserInfo = await data.investContract.methods.getUserInfo(data.userInfo.intro).call()
        if(!introUserInfo.isExist) {
          Toast('推荐人无效')
          return
        }
        try {
          let decimals = await data.abdxContract.methods.decimals().call()
          let amount = new BigNumber(data.amount).multipliedBy(10 ** decimals).toFixed()
          let userLimit = await data.investContract.methods.userLimit(data.address).call();
          let limit =  new BigNumber(userLimit).dividedBy(10 ** decimals).toFixed()
          limit = Number(limit) > 0 ? limit : 100000;
          let cycle = Math.log2(limit > 100000 ? parseInt(limit / 100000) : 1) + 1;
          // if (Number(limit) == Number(data.userInfo.investAmmount)) {
          //    Toast(`第${cycle}轮，质押总量不能超过${limit}ABDX`)
          //    return
          // }
          data.btn.loading = true
          let gas = await data.investContract.methods.invest(data.userInfo.intro, amount).estimateGas({from: data.address})
          console.log(gas)
          await data.investContract.methods.invest(data.userInfo.intro, amount).send({from: data.address})
          data.initData()
          data.btn.loading = false
          Toast('质押成功')
          router.push({name: 'RecordInvest'})
        } catch (error) {
          console.log(error)
          data.btn.loading = false
          Toast('质押失败')
        }
        
      }
    })
    if (data.address) {
      data.initContract()
    }
     watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>

<style lang="less" scoped>
.swap-box{
  padding: 12px 15px;
  .swap{
    background: #fff;
    border-radius: 8px;
    padding: 17px;
    .swap-title{
      margin-bottom: 18px;
      .text{
        color: #111111;
        font-size: 16px;
        font-weight: 600;
      }
      span{
        height: 26px;
        line-height: 24px;
        box-sizing: border-box;
        border-radius: 4px;
        border: 1px solid #2058E9;
        font-size: 14px;
        font-weight: 600;
        color: #2058E9;
        padding: 0 10px;
      }
    }
    .swap-input{
        background: #F4F4F4;
        border-radius: 8px;
        padding: 15px;
        .title{
          margin-bottom: 15px;
          color: #333333;
          font-size: 14px;
        }
        .right-box{
            img{
              width: 22px;
              margin-right: 5px;
            }
            span{
              color: #111111;
              font-size: 15px;
              font-weight: 600;
            }
        }
    }
    .swap-icon{
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 10px;
      img{
          width: 35px;
      }
    }
    .price-box{
        text-align: right;
        color: #111111;
        font-size: 13px;
        margin-top: 14px;
        margin-bottom: 16px;
    }
  }
}
.intro-box{
  color: #111111;
  font-size: 14px;
  font-weight: 600;
  padding: 20px 0 0;
  .title{
    // margin-bottom: 6px;
  }
  .intro{
    border: none;
    height: 44px;
    line-height: 44px;
    border-bottom: 1px solid #DDDDDD;
  }
}
.btn-box{
  padding-top: 15px;
}
.rule-box{
 background: #fff;
 border-radius: 8px;
 padding: 15px;
 .title{
   color: #2058E9;
   font-size: 14px;
   font-weight: 600;
   margin-bottom: 6px;
 }
 .content{
   font-size: 14px;
   line-height: 24px;
   color: #2D1D03; 
 }
}
.total-stake{
  background: #FF9630;
  border-radius: 4px;
  font-size: 10px;
  color: #fff;
  padding: 2px 4px;
  margin-left: 6px;
}
</style>

